"use client";

import { useEvents } from "@/app/hooks/useEvents";

export default function Graficos() {
  const { eventos, colunas } = useEvents();

  if (!eventos.length) return null;

  return (
    <p>
      Gráficos gerados dinamicamente a partir das colunas:
      {colunas.join(", ")}
    </p>
  );
}
